<template>
  <div class="terms-component">
    <div class="terms-title"></div>
    <img class="load-map" src="@/images/join-step-one@2x.png" />
    <div class="terms-content">
      <p class="terms-description">
             <br/>
               .
      </p>
      <p class="all-check"> ,    ()/ () .</p>
      <img class="all-checkbox" :src="checkSelectImg" @click="checkAll" />
      <div class="hr-line"></div>
      <div class="term-box">
        <div class="term-box-title"> ()</div>
        <img class="check-img" :src="firstSelectImg" @click.prevent="first = !first" />
        <div class="conditions">
          <p> 1()</p>   ( )   .            ( )     ( )    ,             .
                           ,        
        </div>
      </div>
      <div class="term-box">
        <div class="term-box-title">      ()</div>
        <img class="check-img" :src="secondSelectImg" @click.prevent="second = !second" />
        <div class="conditions">
          <p>1.  , </p>   ( )   .            ( )     ( )    ,             .
                           ,        
        </div>
      </div>
    </div>
    <button
      class="term-btn"
      :class="{'term-btn-success': !invalidForm}"
      @click.prevent="CHANGE_LOGIN_COMPONENT('regist')"
      :disabled="invalidForm"
    ></button>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      checks: false,
      first: false,
      second: false
    };
  },
  watch: {
    first() {
      if (!this.first) this.checks = false;
    },
    second() {
      if (!this.second) this.checks = false;
    }
  },
  computed: {
    invalidForm() {
      return !this.first || !this.second;
    },
    checkSelectImg() {
      if (!this.checks) return require("@/images/round-check@2x.png");
      else return require("@/images/round-check-selected@2x.png");
    },
    firstSelectImg() {
      if (!this.first) return require("@/images/round-check@2x.png");
      else return require("@/images/round-check-selected@2x.png");
    },
    secondSelectImg() {
      if (!this.second) return require("@/images/round-check@2x.png");
      else return require("@/images/round-check-selected@2x.png");
    }
  },
  methods: {
    ...mapMutations(["CHANGE_LOGIN_COMPONENT"]),
    checkAll() {
      if (this.fisrt || this.second) {
        this.first = false;
        this.second = false;
        this.checks = false;
      } else {
        this.first = true;
        this.second = true;
        this.checks = true;
      }
    }
  }
};
</script>
<style>
.terms-component{
  min-height: calc(100vh - 191px);
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.terms-title {
  width: 115px;
  height: 37px;
  font-family: NanumBarunGothicOTF;
  font-size: 32px;
  text-align: center;
}
.load-map {
  width: 400px;
  height: 72px;
  object-fit: contain;
}
.terms-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}
.terms-description {
  height: 47px;
  font-family: NanumBarunGothicUltraLightOTF;
  font-size: 16px;
  font-weight: 200;
  text-align: center;
  color: #c2c2c2;
}
.all-check {
  width: 542px;
  height: 19px;
  font-family: NanumBarunGothicOTF;
  font-size: 16px;
  text-align: left;
  color: #4b74ff;
}
.all-checkbox {
  width: 24px;
  height: 24px;
  object-fit: contain;
  cursor: pointer;
  text-align: left;
  align-self: flex-start;
}
.hr-line {
  width: 636px;
  height: 0;
  border: solid 0.5px #e2e2e2;
  margin: 5px 0;
}
.term-box {
  height: 166px;
  margin-bottom: 66px;
}
.term-box-title {
  height: 20px;
  font-family: NanumBarunGothicOTF;
  font-size: 18px;
  text-align: left;
  color: #000000;
  margin-bottom: 12px;
}
.check-img {
  width: 24px;
  height: 24px;
  object-fit: contain;
  cursor: pointer;
}
.conditions {
  width: 636px;
  height: 134px;
  border-radius: 3px;
  border: solid 1px #e2e2e2;
  background-color: #ffffff;
  padding: 14px;
  line-height: 1.63;
  overflow: scroll;
}
.term-btn {
  width: 416px;
  height: 64px;
  border-radius: 3px;
  border: none;
  box-shadow: 0 3px 10px 0 rgba(94, 94, 94, 0.6);
  background-color: #a6a6a6;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  text-align: center;
  color: #ffffff;
}
.term-btn-success {
  width: 416px;
  height: 64px;
  border-radius: 3px;
  border: none;
  box-shadow: 0 3px 10px 0 rgba(75, 116, 255, 0.6);
  background-color: #4b74ff;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  text-align: center;
  color: #ffffff;
}
</style>



