<template>
  <div class="find-password">
    <h1 class="find-id-title"> </h1>
    <div>
      <h2 class="id-title"></h2>
      <div class="id-input">
        <input
          type="text"
          autofocus
          placeholder="  "
        />
      </div>
    </div>
    <div>
      <h2 class="pass-name-title"></h2>
      <div class="pass-name-input">
        <input type="text" autofocus placeholder=" " />
      </div>
    </div>
    <div>
      <h2 class="pass-email-title"></h2>
      <div class="pass-email-input">
        <input
          type="text"
          placeholder="    "
        />
      </div>
    </div>
    <div class="pass-check-content">
      <div class="pass-check-num">
        <input type="text" placeholder=" 4" />
      </div>
      <button class="pass-check-num-btn" @click="showImpossible">
         
      </button>
    </div>
    <button class="pass-next-btn" @click="showImpossible"></button>
    <button
      class="pass-chanage-component"
      @click.prevent="CHANGE_FIND_COMPONENT('id')"
    >
       
    </button>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  methods: {
    ...mapMutations(["CHANGE_FIND_COMPONENT"]),
    showImpossible() {
      alert("    .");
    },
  },
};
</script>
<style>
.find-password {
  min-height: calc(100vh - 191px);
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.find-password h1,
.find-password h2 {
  font-weight: normal;
}
.find-password > div {
  margin-bottom: 10px;
}
.find-id-title {
  width: 179px;
  height: 37px;
  font-family: NanumBarunGothicOTF;
  font-size: 32px;
  text-align: center;
  color: #000000;
}

.id-title {
  width: 54px;
  height: 23px;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  text-align: left;
  color: #000000;
}
.pass-name-title {
  width: 54px;
  height: 23px;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  text-align: left;
  color: #000000;
}

.pass-email-title {
  width: 54px;
  height: 23px;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  text-align: left;
  color: #000000;
}

.id-input {
  width: 416px;
  height: 62px;
  border-radius: 3px;
  border: solid 1px #e2e2e2;
  background-color: #ffffff;
}
.pass-name-input {
  width: 416px;
  height: 62px;
  border-radius: 3px;
  border: solid 1px #e2e2e2;
  background-color: #ffffff;
}

.pass-email-input {
  width: 416px;
  height: 62px;
  border-radius: 3px;
  border: solid 1px #e2e2e2;
  background-color: #ffffff;
}

.pass-check-content {
  display: flex;
}

.pass-check-num {
  width: 264px;
  height: 62px;
  border-radius: 3px;
  border: solid 1px #e2e2e2;
  background-color: #ffffff;
}

.id-input input,
.pass-name-input input,
.pass-email-input input {
  outline: none;
  border: none;
  margin-top: 24px;
  margin-left: 19px;
  font-size: 16px;
  width: 300px;
  font-family: NanumBarunGothicUltraLightOTF;
}
.pass-check-num input {
  outline: none;
  border: none;
  margin-top: 24px;
  font-size: 16px;
  width: 200px;
  font-family: NanumBarunGothicUltraLightOTF;
  padding: 0 20px;
}

.pass-check-num-btn {
  width: 140px;
  height: 62px;
  border-radius: 3px;
  border: solid 2px #4b74ff;
  font-family: NanumBarunGothicOTF;
  font-size: 16px;
  line-height: 2.81;
  text-align: center;
  color: #4b74ff;
  margin-left: 13px;
}
.pass-next-btn {
  width: 416px;
  height: 64px;
  border-radius: 3px;
  box-shadow: 0 3px 10px 0 rgba(75, 116, 255, 0.6);
  background-color: #4b74ff;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  line-height: 1.6;
  text-align: center;
  color: #ffffff;
  margin-bottom: 10px;
}
.pass-chanage-component {
  width: 416px;
  height: 64px;
  border-radius: 3px;
  border: solid 1px #4b74ff;
  background-color: #ffffff;
  font-family: NanumBarunGothicOTF;
  font-size: 20px;
  line-height: 1.6;
  text-align: center;
  color: #4b74ff;
}
</style>




